

































import segmentedControl from '../../../components/segmented-control.vue';
export default {
	data() {
		return {
			tabitems: [
				'收入',
				'支出'
			],
			current: 0,
			activeColor: '#17393c',
			styleType: 'text',
			scoreCount: '', //总条数
			totalScore: '', //总积分数
			add_list: [],
			reduce_list: []
		};
	},
	onLoad: function() {
		this.getInitData(0, 20, 1);
	},
	methods: {
		getInitData(type, limit, pageNum) {
			let that = this;
			let data = {
				userCode: this.$store.state.userCode,
				token: this.$store.state.token,
				type: type,
				limit: limit,
				pageNum: pageNum
			}
			this.$api.myScoreInfo(data, function(res) {
				if (res.data.status == 1) {
					that.scoreCount = res.data.data.scoreCount;
					that.totalScore = res.data.data.totalScore;
					that.listItem(type, res.data.data.scoreList);
				}
				console.log(res)
			}, function(err) {
				console.log(err)
			});
		},
		listItem(type, value) {
			let list_type = '';
			let that = this;
			if (type == 0) {
				list_type = 'add_list';
			} else {
				list_type = 'reduce_list';
			}
			if (Array.isArray(value)) {
				let arr = value;
				value.map(function(item, index) {
					arr[index].createDate = value[index].createDate.substring(0, 10)
				});
				this[list_type] = arr;
			}
		},
		onClickItem(index) {
			console.log(index)
			if (this.current !== index) {
				this.current = index;
			}
			this.getInitData(index, 20, 1);
		},
		formatDate(data) {
			return data.toString().slice(0, 10);
		}
	},
	components: {
		segmentedControl
	}
}



